import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Markup } from 'interweave';
import { getCMSPageSaga, resetCMS } from '../../../store/actions';

// eslint-disable-next-line no-unused-vars
const MarketingOfferTerms = props => {
  const dispatch = useDispatch();

  // eslint-disable-next-line no-unused-vars
  const { CMSContent, isCMSLoaded } = useSelector(state => state.cms);

  useEffect(() => {
    dispatch(getCMSPageSaga('offerPolicy'));
    return () => dispatch(resetCMS());
  }, []);

  return (
    <div id="wrapper">
      <div className=" coman-modal coman-modal-a" id="sign-in">
        <div
          className="modal-content  text-center modal-dialog  modal-dialog-centered"
          role="document"
          style={{ textAlign: 'center', width: 'auto' }}
        >
          <a>
            <img
              src={require('../../../assets/img/logo-modal.png')}
              alt=""
              style={{ marginBottom: '29px', width: 80 }}
            />
          </a>
          <h1>Offer Terms</h1>
          <div
            className="container"
            style={
              isCMSLoaded
                ? { textAlign: 'justify', minHeight: 250 }
                : { textAlign: 'center', padding: '125px 0px' }
            }
          >
            {isCMSLoaded ? (
              <Markup content={CMSContent.content} />
            ) : (
              'Loading...'
            )}
          </div>
          <div style={{ marginTop: '87px' }}>
            &copy; Copyright 2020. All Rights Reserved
          </div>
        </div>
      </div>
    </div>
  );
};

export default MarketingOfferTerms;
